.about {
    display: flex;
    color: var(--colorRed);
    flex-direction: column;
    padding-inline: 108px;
    margin-top: 15px;
}

.flex {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
}


@media (max-width: 768px) { 
    .about { padding-inline: 0; padding-bottom: 100px; }
}